import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
// import { useEffect } from "react";
const prohibit = () => {
    const navigate = useNavigate();
    Swal.fire({
        title: " .",
        text: " .       .",
        icon: "error",
        confirmButtonText: "",
    }).then(() => {
        navigate(-1);
    });
};

export default prohibit;
