import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
// import { useEffect } from "react";
const notfound_alarm = () => {
    const navigate = useNavigate();
    Swal.fire({
        title: "404 Not Found",
        text: "   .       .",
        icon: "error",
        confirmButtonText: "",
    }).then(() => {
        navigate(-1);
    });
};

export default notfound_alarm;
